import { lazy, useMemo } from "react"
import { useParams, useHistory } from "react-router-dom"
import { encodeObj, decodeObj } from "../../utils"

const activities = {
  question: lazy(() => import("./Question")),
  simonSays: lazy(() => import("./SimonSays")),
  buttonClicks: lazy(() => import("./ButtonClicks")),
  pokemonTrivia: lazy(() => import("./PokemonTrivia")),
  qrScan: lazy(() => import("./QRScan")),
  redirect: (props) => {
    const { url = "/NotFound" } = props
    window.location.replace(url)
    return null
  },
}

const Game = () => {
  const { code } = useParams()
  const history = useHistory()
  const steps = useMemo(() => decodeObj(code), [code])
  const step = steps[steps.length - 1]

  const Component = activities[step.type]

  const handleDone = () => {
    const url = encodeObj(steps.slice(0, -1))
    history.push(`/${url}`)
  }

  return <Component {...step} onDone={handleDone} />
}

export default Game
